<template>
  <div class="ImageLoader-container">
    <img :src="src" alt="" @load="showImage" />
    <img
      v-if="!isOver"
      :src="placeholder"
      alt=""
      class="showTest"
      :style="{ opacity: opactiyShow, transition: `${duration}ms` }"
    />
  </div>
</template>

<script>
export default {
  props: {
    src: {
      //原始图片路径;
      type: String,
      required: true,
    },
    placeholder: {
      //占位图片
      type: String,
      required: true,
    },
    duration: {
      //原始图片和占位图片过渡时间！
      type: Number,
      default: 500,
    },
  },
  methods: {
    showImage() {
      this.transitions = true;
      setInterval(() => {
        this.isOver = true;
      }, this.duration);
    },
  },
  data() {
    return {
      transitions: false,
      isOver: false,
    };
  },
  computed: {
    opactiyShow() {
      return this.transitions ? 0 : 1;
    },
  },
};
</script>

<style scoped lang="less">
.ImageLoader-container {
  position: relative;
  overflow: hidden;
  display: flex;
  margin: 0 auto;
  height: 100%;
  width: 100%;
  //   border: 1px solid red;
}
.ImageLoader-container img {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
}
</style>
