<template>
  <div
    ref="container"
    class="home-container"
    @wheel="hanleWheel"
    v-loading="loading"
  >
    <ul class="images-container" :style="{ marginTop }" @transitionend="isover">
      <!-- 通过绑定style，来控制marginTop（注意，这里的marginTop是组合写法，控制上边距用margintop，
      但在这，计算属性中的一个函数也是marginTop，所以这里可以直接写一个marginTop） -->
      <li v-for="i in data" :key="i.id">
        <!-- 循环生成li，li数量取决外部异步获取的数组长度-->
        <CarouselItem :Carousel="i" />
      </li>
    </ul>

    <!-- --------------------  这里是分隔符  -------------------- -->

    <div class="arrowUp abs" @click="switchTo(index - 1)" v-show="index >= 1">
      <!-- 这是上下按钮，点击时触发switchTo函数，把新的值传进去(页面具体显示那个图片，取决于index的值，)
      这里是向上切换，所以index要加1，(图片从下往上排列，高度==0 是第一张，高度==-一张图片高度，是第二张)
       -->
      <!-- 并且是否显示是看，当前索引值是否是大于1，默认是0，如果当前是1，代表有向上按的条件! -->
      <Icon type="arrowUp" />
    </div>
    <div
      class="arrowDown abs"
      @click="switchTo(index + 1)"
      v-show="index < data.length - 1"
    >
      <Icon type="arrowDown" />
    </div>
    <ul class="btn-container">
      <li
        :class="{ active: index === i }"
        v-for="(item, i) in data"
        :key="item.id"
        @click="switchTo(i)"
      >
        <!--
        这是右侧按钮,
        是否是选中状态取决于当前下表是不是等于index，
        点击切换只要把当前下表传进去就好来!
        -->
      </li>
    </ul>
  </div>
</template>

<script>
import CarouselItem from "./CarouselItem";
import Icon from "../../components/Icon";
import { mapState } from "vuex";
export default {
  components: {
    Icon,
    CarouselItem,
    //局部注册组件
  },
  data() {
    return {
      index: 0,
      containerHeight: 0,
      //添加2数据，
      //index === 当前显示的是第几页
      //containerHeight === 一页的高度
      switchIng: false,
      timer: null,
    };
  },
  mounted() {
    this.containerHeight = this.$refs.container.clientHeight;
    //生成真实Dom之后触发该函数，
    //把一页的高度直接赋值给containerHeight;

    window.addEventListener("resize", this.handleResize);
    this.handleAuto();
  },
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
  },
  created() {
    this.$store.dispatch("banner/fetchBanner");
  },
  computed: {
    marginTop() {
      return -this.index * this.containerHeight + "px";
      //计算属性，当index数据被修改时，重新渲染
    },
    ...mapState("banner", ["loading", "data"]),
  },
  methods: {
    switchTo(i) {
      this.index = i;
      //修改index值,直接把调用该函数时传进来的i 赋值给index，只要index值一改变，vue重新计算值，
    },
    hanleWheel(e) {
      if (this.switchIng) {
        //加锁，如果switchIng == true；代表正在滚动中，再次滚动没有任何效果
        return;
      }
      if (e.deltaY < -5 && this.index > 0) {
        //e.deltaY拿到的是每次滚动的距离
        //向上滚动
        this.switchIng = true;
        this.index--;
      } else if (e.deltaY >= 5 && this.index < this.data.length - 1) {
        //向下滚动
        this.switchIng = true;
        this.index++;
      }
    },
    handleAuto() {
      this.timer = setInterval(() => {
        if (this.switchIng) {
          return;
        } else {
          this.index++;
          if (this.index == this.data.length) {
            this.index = 0;
          }
        }
      }, 10000);
    },
    isover() {
      //当滚动动画完成之后将switchIng设置为false
      this.switchIng = false;
    },
    handleResize() {
      this.containerHeight = this.$refs.container.clientHeight;
    },
  },
};
</script>

<style lang="less" scoped>
li {
  list-style: none;
}
.home-container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.abs {
  position: absolute;
  left: 50%;
  font-size: 30px;
  cursor: pointer;
  color: #fff;
  transform: translateX(-50%);
}
.arrowUp {
  top: 30px;
  animation: jumUp 1.5s infinite;
}
.arrowDown {
  bottom: 30px;
  animation: jumDown 1.5s infinite;
}
@keyframes jumUp {
  0% {
    transform: translateY(15px);
  }
  50% {
    transform: translateY(-15px);
  }
  100% {
    transform: translateY(15px);
  }
}
@keyframes jumDown {
  0% {
    transform: translateY(-15px);
  }
  50% {
    transform: translateY(15px);
  }
  100% {
    transform: translateY(-15px);
  }
}
.btn-container li {
  width: 13px;
  height: 13px;
  border-radius: 50%;
  margin-top: 10px;
  border: 2px solid rgb(143, 120, 120);
  background: rgb(179, 162, 162);
  overflow: hidden;
}
.btn-container {
  position: absolute;
  right: 20px;
  top: 50%;
  cursor: pointer;
  transform: translateY(-50%, -50%);
}
.images-container {
  width: 100%;
  transition: 0.5s;
  height: 100%;
}
.images-container li {
  width: 100%;
  height: 100%;
}
.active {
  background: rgb(34, 180, 248) !important;
}
</style>
