<template>
  <div
    class="CarouselItem-container"
    ref="container"
    @mousemove="handleMousemove"
    @mouseleave="center"
  >
    <div
      class="CarouselItem-images"
      ref="imageContainer"
      :style="imagePosition"
    >
      <ImageLoader
        :src="Carousel.bigImg"
        :placeholder="Carousel.midImg"
        class="images"
      />
      <div class="title Position" ref="titles">
        {{ Carousel.title }}
      </div>
      <div class="des Position" ref="des">
        {{ Carousel.description }}
      </div>
    </div>
  </div>
</template>

<script>
import ImageLoader from "@/components/ImageLoader";
export default {
  props: ["Carousel"],
  components: {
    ImageLoader,
  },
  data() {
    return {
      titleWidth: 0,
      desWidth: 0,
      containerSize: null,
      innerSize: null,
      mouseX: 0,
      mouseY: 0,
    };
  },
  computed: {
    imagePosition() {
      if (!this.innerSize || !this.containerSize) {
        return;
      }
      const eactWidth = this.innerSize.width - this.containerSize.width;
      const eactHeight = this.innerSize.height - this.containerSize.height;
      const left = (-eactWidth / this.containerSize.width) * this.mouseX;
      //left / this.mouseX = -eactWidth / this.containerSize.width
      //所以 left = -eactWidth / this.containerSize.width * this.mouseX;
      const top = (-eactHeight / this.containerSize.height) * this.mouseY;
      return {
        left: left + "px",
        top: top + "px",
      };
    },
  },
  mounted() {
    this.titleWidth = this.$refs.titles.clientWidth;
    this.desWidth = this.$refs.des.clientWidth;
    this.setSize();
    this.center();
    this.showTitle();
    window.addEventListener("resize", this.setSize);
  },
  destroyed() {
    window.removeEventListener("resize", this.setSize);
  },
  methods: {
    showTitle() {
      this.$refs.titles.style.opacity = 1;
      this.$refs.titles.style.width = 0;
      this.$refs.titles.clientWidth;
      this.$refs.titles.style.transition = "1.4s 1s";
      this.$refs.titles.style.width = this.titleWidth + "px";
      this.$refs.des.style.opacity = 1;
      this.$refs.des.style.width = 0;
      this.$refs.titles.clientWidth;
      this.$refs.des.style.transition = "1.4s 1.4s";
      this.$refs.des.style.width = this.desWidth + "px";
    },
    setSize() {
      this.containerSize = {
        width: this.$refs.container.clientWidth,
        height: this.$refs.container.clientHeight,
      };
      this.innerSize = {
        width: this.$refs.imageContainer.clientWidth,
        height: this.$refs.imageContainer.clientHeight,
      };
    },
    handleMousemove(e) {
      const rect = this.$refs.container.getBoundingClientRect();
      this.mouseX = e.clientX - rect.left;
      this.mouseY = e.clientY - rect.top;
    },
    center() {
      this.mouseX = this.containerSize.width / 2;
      this.mouseY = this.containerSize.height / 2;
    },
  },
};
</script>

<style lang="less" scoped>
.CarouselItem-container {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.CarouselItem-container .CarouselItem-images {
  width: 110%;
  height: 110%;
  min-width: 700px;
  min-height: 700px;
  transition: 0.5s; // overflow: hidden;
  position: absolute;
}

.Position {
  position: absolute;
  top: 0;
  color: #fff;
  white-space: nowrap;
  overflow: hidden;
  text-shadow: 5px 0 3px rgba(0, 0, 0, 0.4), 0px 5px 3px rgba(0, 0, 0, 0.4),
    -5px 0px 3px rgba(0, 0, 0, 0.4), 0px -5px 3px rgba(0, 0, 0, 0.4);
}

.title {
  top: 47%;
  left: 100px;
  opacity: 0;
  font-size: 2em;
}

.des {
  top: 55%;
  left: 100px;
  opacity: 0;
  font-size: 1.6em;
}
</style>
